<template>
    <main-container :is-collpased="isCollapsed">
        <template slot="sidebar">
        </template>
        <template slot="content">
            <Row :gutter="8">
                <i-col :span="18" style="text-align: center">
                    <Card :padding="8">
                        <h4>{{album.name}}</h4>
                        <Page :total="imgCountTotal" :page-size="imgCountPerPage" prev-text="上一页" next-text="下一页"
                              @on-change="hanglePaginate" :current="currentPage"/>
                    </Card>
                    <Row v-for="item in images">
                        <img :src="item.linkimg" style="max-width: 100%"/>
                    </Row>
                    <Row>
                        <Page :total="imgCountTotal" :page-size="imgCountPerPage" show-total prev-text="上一页"
                              next-text="下一页"
                              @on-change="hanglePaginate" :current="currentPage"/>
                    </Row>
                    <Row v-if="images.length <= 4">
                        <Divider><h4>【{{album.publisher.name}}】出品的专辑</h4></Divider>
                        <Card shadow style="margin: 8px;width: 252px; float: left;"
                              v-for="(item, idx) in publisherAlbums" :key="item.id">
                            <h4 slot="title">{{item.name}}</h4>
                            <div style="text-align:center">
                                <router-link :to="`/album/` + item.id" target="_blank">
                                    <img :src="item.cover" width="200px" height="250px"/>
                                </router-link>
                            </div>
                        </Card>
                    </Row>
                </i-col>
                <i-col :span="6">
                    <Row>
                        <Divider><h4>模特儿【{{moter.name}}】的其他专辑</h4></Divider>
                        <Card v-for="item in moterAlbums">
                            <div style="text-align:center">
                                <router-link :to="`/album/` + item.id" target="_blank">
                                    <img :src="item.cover" style="max-width: 100%; height: 100%"/>
                                </router-link>
                            </div>
                        </Card>
                    </Row>
                    <Row v-if="images.length > 4">
                        <Divider><h4>【{{album.publisher.name}}】出品的专辑</h4></Divider>
                        <Card v-for="item in publisherAlbums" :padding="16">
                            <router-link :to="`/album/` + item.id" target="_blank">
                                <div style="text-align:center">
                                    <h5>{{item.name}}</h5>
                                </div>
                            </router-link>
                        </Card>
                    </Row>
                </i-col>
            </Row>

        </template>
    </main-container>
</template>

<script>
    import {getAlbums, getImages} from "../api/api";

    export default {
        name: "album-detail",
        data() {
            return {
                isCollapsed: true,
                albumId: 0,
                album: Object,
                moter: Object,
                publisher: Object,
                images: [],
                imgCountTotal: 0,
                imgCountPerPage: 6,
                pageCount: 0,
                currentPage: 1,
                moterAlbums: [],
                publisherAlbums: [],
            }
        },
        computed: {},
        methods: {
            obtainAlbums() {
                getAlbums({id: this.albumId}).then(
                    (response) => {
                        this.album = response.data;
                        this.moter = response.data.moter;
                        this.publisher = response.data.publisher;
                        getAlbums({params: {moter: this.moter.id}}).then(
                            (response) => {
                                this.moterAlbums = response.data.results;
                            }
                        );
                        getAlbums({params: {publisher: this.publisher.id}}).then(
                            (response) => {
                                this.publisherAlbums = response.data.results;
                            }
                        );
                    }
                );
            },
            obtainImages(queryParams) {
                getImages({params: queryParams}).then(
                    (response) => {
                        this.images = response.data.results;
                        this.imgCountTotal = response.data.count;
                        this.pageCount = Math.ceil(this.imgCountTotal / this.imgCountPerPage)
                    }
                );
            },
            hanglePaginate(current) {
                this.currentPage = current;
                const msg = this.$Message.loading({
                    content: '正在加载中...',
                    duration: 0
                });
                setTimeout(msg, 2500);
                this.obtainImages({album: this.albumId, page: this.currentPage});
            }
        },
        created() {
            this.albumId = this.$route.params.id;
            this.obtainAlbums();
            this.obtainImages({album: this.albumId, page: this.currentPage});
        }
    }
</script>

<style scoped>

</style>